import * as React from 'react';
import { css } from 'office-ui-fabric-react';
import Strings from '../loc/Strings.resx';
import propertyPaneStyles from '../propertyPane/PropertyPane.module.scss';
import propertyPaneTitleBarStyles from './PropertyPaneTitleBar.module.scss';
import { StringHelper } from '@ms/odsp-utilities-bundle';

export interface IPropertyPaneTitleBarProps {
  title: string;
  onClose: () => void;
}

export default class PropertyPaneTitleBar extends React.Component<IPropertyPaneTitleBarProps, void> {

  public render(): React.ReactElement<{}> {

    return (
      <div className={ propertyPaneTitleBarStyles.propertyPaneTitleBarBox }>
        <div className={ propertyPaneTitleBarStyles.propertyPaneTitleBar }>
          <div className={
            css(propertyPaneTitleBarStyles.propertyPanePageTitle, 'ms-font-xl', 'ms-fontColor-white') }>
            { this.props.title }
          </div>

          <div className={ propertyPaneStyles.propertyPaneScreenReaderOnly } id='propertyPaneTitleId'>
            { StringHelper.format( Strings.PropertyPaneTitle, this.props.title) }
          </div>

          <button
            tabIndex={ this.props.title ? 0 : -1 }
            className={ propertyPaneTitleBarStyles.propertyPaneClose }
            onClick={ this.props.onClose }
            aria-label={ Strings.Close }>
              <i className={ css(propertyPaneTitleBarStyles.closeButton, 'ms-Icon', 'ms-Icon--Cancel') }></i>
          </button>
        </div>
      </div>
    );
  }
}
